// console.log(API)
const CODE = 1
/*
 *  rem  ---- iphone6 1rem === 50px
 * @param { String } grid ===> 7.5
 */
const rem = grid => {
  let html = document.documentElement
  let equipmentWidth = html.getBoundingClientRect().width
  if (equipmentWidth > 640) {
    equipmentWidth = 640
  }
  html.style.fontSize = (equipmentWidth / grid) + 'px'
}

/*
 * 
 * @param { Number } code ===> 
 * @param { String } msg ===> 
 * @param { Boolean } true false 
 */
function resErr (code, msg, error) {
  if (CODE !== code && code !== undefined) {
    vue.$vux.toast.text && vue.$vux.toast.text(msg)
    return true
  }

  return false
}
/*
  * @param {DOM Element } args[0] ===>  className DOM 
  * @param { String } args[1] ===>  className
*/
function addClass (...args) {
    // 1. 2.  
  let nameArr = args[0].className.split(' ')
    // 
  for (let i = 0, len = nameArr.length; i < len; i++) {
    if (nameArr[i] === args[1]) return false
  }
    // 3.push
  nameArr.push(args[1])
    // 4.  5.
  args[0].className = nameArr.join(' ')
}

/*
  * @param {DOM Element | Array } args[0] DOM    ps:  Array  
  *
*/
function removeClass (...args) {
    //  dom DOM
    // 1.args[0] dom DOM
  if (args[0] instanceof Array) {
      // args[0] 
    for (let i = 0, len = args[0].length; i < len; i++) {
      let name = args[0][i].className.split(' ')
      for (let j = 0, jLen = name.length; j < jLen; j++) {
        if (args[1] === name[j]) {
            // 
          name.splice(j, 1)
          args[0][i].className = name.join(' ')
        }
      }
    }
  } else if (args[0] instanceof Object) {
      // args[0] DOM
      // 1.DOMclassName 2. 
    let name = args[0].className.split(' ')
    for (let i = 0, len = name.length; i < len; i++) {
      if (args[1] === name[i]) {
          // 
        name.splice(i, 1)
          // DOM
        args[0].className = name.join(' ')
      }
    }
  }
}

/*
* @param { DOM Element} el ===> DOM 
* @return { Array }  DOM  
*/
function siblings (el) {
    // 
  if (!el) return false
    // ,
  let allChild = el.parentNode.children
  let arr = []
    // 
  for (let i = 0, len = allChild.length; i < len; i++) {
    if (el !== allChild[i]) arr.push(allChild[i])
  }
  return arr
}

/**
 * 
 * @parma { Function } set ===> , keyvalue
 * @param { Function } get ===> key,  value, undefined
 * @param { Function } remove ===> , key, 
 */
const localData = (() => {
  return {
    hname: location.hostname ? location.hostname : 'localStatus',
    isLocalStorage: window.localStorage || false,
    dataDom: null,
    initDom: function () { // userData
      if (!this.dataDom) {
        try {
          this.dataDom = document.createElement('input') // hiddeninput
          this.dataDom.type = 'hidden'
          this.dataDom.style.display = 'none'
          // userData IE 
          this.dataDom.addBehavior('#default#userData') // userData
          document.body.appendChild(this.dataDom)
          var exDate = new Date()
          exDate = exDate.getDate() + 30
          this.dataDom.expires = exDate.toUTCString() // 
        } catch (ex) {
          return false
        }
      }
      return true
    },
    // 
    set: function (key, value) {
      if (this.isLocalStorage) {
        window.localStorage.setItem(key, value)
      } else {
        if (this.initDom()) {
          this.dataDom.load(this.hname)
          this.dataDom.setAttribute(key, value)
          this.dataDom.save(this.hname)
        }
      }
    },
    // 
    get: function (key) {
      if (this.isLocalStorage) {
        return window.localStorage.getItem(key)
      } else {
        if (this.initDom()) {
          this.dataDom.load(this.hname)
          return this.dataDom.getAttribute(key)
        }
      }
    },
    // 
    remove: function (key) {
      if (this.isLocalStorage) {
        localStorage.removeItem(key)
      } else {
        if (this.initDom()) {
          this.dataDom.load(this.hname)
          this.dataDom.removeAttribute(key)
          this.dataDom.save(this.hname)
        }
      }
    }
  }
})()

 // useragent
function getUseragent () {
  var ua = navigator.userAgent
  if (ua.match(/(iPhone|Android)/i)) {
    if (ua.indexOf('wtsh_ios') > -1) {
      // window.alert('ios')
      return 'ios'
    } else if (ua.indexOf('wtsh_android') > -1) {
      // window.alert('android')
      return 'android'
    } else {
      return 'webapp'
    }
  } else {
    return 'pc'
  }
}

// app01
function isInApp () {
  let type = getUseragent()
  if (type === 'ios' || type === 'android') {
    return 1
  } else {
    return 0
  }
}
