function ajaxFn (url, data, method = 'post') {
  return new Promise((resolve, reject) => {
    axios({
      method: method,
      url: network(url),
      data: data
    }).then(({ data: { data, code, message } }) => {
      if (resErr(code, message)) return false
      resolve(data)
    }).catch(err => {
      vue.$vux.toast.text(err.message)
      reject(err)
    })
  })
}
