/**
 * --begin--
 * 
 */
const obj = {
  validNum: {
    regex: /^\d+(\.\d+)?$/,
    error: ""
  },
  validInteger: {
    regex: /^[1-9]\d*$/,
    error: ""
  },
  validEmail: {
    regex: /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/,
    error: ""
  },
  validTime: {
    regex: /\d{4}-\d{2}-\d{2}\s+\d{2}:\d{2}/,
    error: ""
  },
  validId: {
    regex: /(^[0-9a-zA-Z]{6,}$)/, // 
    error: ""
  },
  validPrice: {
    regex: /^([1-9][\d]{0,10}|0)([.]?[\d]{1,2})?$/,
    error: ""
  },
  validMobile: { // 
    regex: /^(13[0-9]|14[5|7]|15[0-9]|16[0-9]|17[1-9]|18[0-9]|19[0-9])\d{8}$/,
    error: ""
  },
  validPhone: {
    regex: /^(\(\d{3,4}\)|\d{3,4}(-|\s)?)?\d{7,8}(-\d{1,4})?$/,
    error: ""
  },
  validPostalCode: {
    regex: /^\d{6}$/,
    error: "6"
  },
  validZipCode: {
    regex: /^\d{6}$/,
    error: "6"
  },
  validWeChat: {
    regex: /^[a-zA-Z\d_-]{5,}$/,
    error: ""
  },
  validName: {
    regex: /^[A-Za-z0-9\u4e00-\u9fa5_-]{1,}$/,
    error: ""
  }
}

function validity (rule) {
  return rule.required
  ? obj[rule.type].regex.test(rule.value)
  : rule.value === "" || obj[rule.type].regex.test(rule.value)
}

const dataValidity = (rules) => {
  let state = {
    status: !0
  }
  for (let i in rules) {
    let type = rules[i].type
    if (typeof obj[type] === "undefined") { // type
      state = {
        status: !1,
        error: rules[i].name + "type",
        index: i
      }
      break
    } else if (rules[i].required && rules[i].value === "") { // 
      state = {
        status: !1,
        error: rules[i].name + "",
        index: i
      }
      break
    } else if (!validity(rules[i])) { // 
      state = {
        status: !1,
        error: obj[type].error,
        index: i
      }
      break
    }
  }
  return state
}
// --over--
/**
 * 
 * @param {string} name 
 * @param {string} url 
 * @return {string}  value
 * @example getQuery('id','http://xx.com?id=1')  => 1
 * ?,
 */
const getQuery = (name = '', url = '') => {
  let match = /^[hH][tT][tT][pP]([sS]?):\/\/(\S+\.)+\S{2,}$/ // 
  if (!match.test(url) || (url.indexOf('?') === -1)) return
  let search = url.split('?')[1]
  let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)")
  let r = search.match(reg)
  if (r != null) return unescape(r[2])
  return null
}

