import Axios from "@/util/request"
export const getUser = data => Axios({
  method: 'post',
  url: '/users/view',
  data
})

// 
export const createCode = data => Axios({
  method: 'post',
  url: '/users/createCode',
  data
})

// 
export const userinfoUpdate = data => Axios({
  method: 'post',
  url: '/users/userinfoUpdate',
  data
})



